@mixin base() {
    background-repeat: no-repeat;
    background-size: cover;
    padding: 20rem 0;
    text-align: center;

    @media screen and (min-width: 768px) {
        background-size: 100%;
    }
}

.page-header--home {
    background-color: transparent;
    display: flex;
    justify-content: center;
    padding-bottom: 2rem;
    padding-top: 2rem;
    position: fixed;
    transition: background-color 0.2s linear, padding-bottom 0.2s linear, padding-top 0.2s linear;
    width: 100%;
    z-index: 999;


    .page-header__logo,
    .page-header__logo2 {
        margin: 0 5rem;
        text-align: center;
        transition: transform 0.2s linear;
        max-width: 10rem;
        // transition: max-width 0.75s;

        @media all and (min-width: 768px) {
            max-width: 20rem;
        }
    }

    &.scrolling {
        background-color: HSLA(203, 100%, 27%, 1.00);
        padding-bottom: 0.5rem;
        padding-top: 0.5rem;
        // padding-bottom: 1rem;
        // padding-top: 1rem;
        // position: fixed;
        // transition: background-color 0.5s;

        .page-header__logo,
        .page-header__logo2 {
            transform: scale(0.6);
            // max-width: 10rem;
            // transition: max-width 0.75s;
            // vertical-align: bottom;
        }
    }
}

.page-header--colonial-life {
    @include base;
    background-image: url('../images/Masthead 2.jpg');
    background-position: 50% 40%;
}

.page-header__logo {
    max-width: 70%;
}

.page-header--unum {
    @include base;
    background-image: url('../images/unum-header_sm.jpg');
    background-position: 50% 50%;

    @media screen and (min-width: 1320px) {
        background-position: 50% 40%;
    }

    &__logo {
        margin-right: 0;
        height: auto;

        @media all and (min-width: 768px) {
            height: 8rem;
        }
    }
}

.back-link {
    color: white;
    font-size: 2rem;
    opacity: 0.85;
    padding: 1rem;
    position: fixed;
    left: 0;
    text-decoration: none;
    top: 0;
    z-index: 999;

    &__image {
        margin-right: 1rem;
        vertical-align: bottom;
        width: 3rem;
    }
}