@mixin base() {
    width: 50%;
    margin: 0 auto;
    display: block;
    width: 75%;

    @media all and (min-width: 1024px) {
        margin-left: 5rem;
        float: right;
        width: 50%;
    }
}

&__image {
    @include base;

    &--fullwidth {
        @include base;
        @media all and (min-width: 1024px) {
            float: none;
            margin: 0 auto;
        }
    }
}